div {
  box-sizing: border-box;
}

button {
  box-sizing: border-box;
  appearance: none;
}

input {
  border: 0;
  outline: 0;
  box-sizing: border-box;
}

.wrapper {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #555;
}

.root {
  width: 100vw;
  height: 100%;
  //border: 1px solid #bababa;
  font-family: 'Play', sans-serif;
  font-size: 18px;
  display: flex;
  flex-direction: column;
}

.display {
  flex-grow: 2;
  width: 100%;
  height: 60px;
  text-align: right;
  font-size: 2em;
  padding: .4em 1em;
  background-color: #fff;
}

.numBlock {
  display: flex;
  flex-direction: column;
}

.row {
  width: 100%;
  display: flex;
  flex-direction: row;
  border: 0;
}

.cell {
  text-align: center;
  background-color: #fff;
  border: 0;
  border-top: 1px solid #ddd;
  border-right: 1px solid #ddd;

  flex: 1;
  overflow: hidden;
  margin: 0;
  padding: 5px 0;

  user-select: none;
  cursor: pointer;
  outline: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

  font-family: 'Play', sans-serif;
  font-size: 2.2em;
  color: #444;

  &:last-of-type {
    border-right: none;
  }

  &:active {
    background-color: #ddd;
    outline: 0;
  }

  &[data-type="system"] {
    background-color: #7eaaea;
    color: #fff;
    font-size: 1.8em;

    &:active {
      background-color: #6992ce;
    }
  }
}


.opBlock {
  display: flex;
  flex-direction: column;

  & .cell {
    margin: .2em 0 0 .2em;
    padding: .4em 0;
    font-size: 1.2em;
    color: #eee;
    background-color: #333;
    border: 1px solid #777;

    &:last-of-type {
      margin-right: .2em;
    }

    &:active {
      background-color: #555;
    }
  }

  & .row:last-of-type > .cell {
    margin-bottom: .2em;
  }
}